import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import configEdit from './views/config/edit.vue'
import actionLogList from './views/action/actionLogList.vue'
import memberActionLog from './views/action/memberActionLog.vue'
import actionList from './views/action/list.vue'
import scoreRuleList from './views/scoreRule/list.vue'
import cmsArticleCate from './views/cms/articleCate.vue'
import cmsArticle from './views/cms/article.vue'
import Member from './views/member/list.vue'
import MemberManager from './views/member/managerList.vue'
import Weibo from './views/weibo/list.vue'
import Group from './views/group/list.vue'



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        leaf: true,//
        children: [
            { path: '/main', component: Main, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-cog',//class
        children: [
            { path: '/configEdit', component: configEdit, name: '' },
            { path: '/scoreRuleList', component: scoreRuleList, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-compass',//class
        children: [
            { path: '/actionList', component: actionList, name: '' },
            { path: '/actionLogList', component: actionLogList, name: '' },
            { path: '/memberActionLog', component: memberActionLog, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-book',
        children: [
            { path: '/cms/articleCate', component: cmsArticleCate, name: '' },
            { path: '/cms/article', component: cmsArticle, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-user-circle',
        children: [
            { path: '/member/list', component: Member, name: '' },
            { path: '/member/managers', component: MemberManager, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-weibo',
        children: [
            { path: '/weibo/list', component: Weibo, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-group',
        children: [
            { path: '/group/list', component: Group, name: '' }
        ]
    }
];

export default routes;


